import React from "react";
import ReactDOM from "react-dom/client";
import "./index.css";
// import App from './App';
import reportWebVitals from "./reportWebVitals";
import {
  createBrowserRouter,
  createHashRouter,
  createMemoryRouter,
  isRouteErrorResponse,
  RouterProvider,
  useRouteError,
} from "react-router-dom";
import LoadingPage from "./pages/loading";
import MemberPage from "./pages/members";
import HomePage from "./pages/home";
import IndividualMemberPage from "./pages/member";
import ShowsPage from "./pages/shows";
import IndividualShowPage from "./pages/show";
import Test from "./components/test";
import FullPlayer from "./pages/fullplayer";

function ErrorElem() {
  let error = useRouteError();
  console.error(error);
  if (isRouteErrorResponse(error)) {
    console.log("ROUTE ERROR");
  }
  return (
    <div>
      <div>{JSON.stringify(error)}</div>
    </div>
  );
}

// oh to use createBrowserRouter
// but alas, we need a proper server and what not
// i mean, we can use gh pages, but i can't get it to work reliably, so it goes away!
const router = createMemoryRouter(
  [
    {
      path: "/",
      element: <HomePage />,
      errorElement: <ErrorElem />,
    },
    {
      path: "/player",
      element: <FullPlayer />,
    },
    {
      path: "/schedule",
      element: <Test />,
    },
    {
      path: "/schedule/shows",
      element: <ShowsPage />,
    },
    {
      path: "/schedule/shows/:name",
      element: <IndividualShowPage />,
    },
    {
      path: "/members",
      element: <MemberPage />,
    },
    {
      path: "/members/:name",
      element: <IndividualMemberPage />,
    },
    {
      path: "/contact",
      element: <LoadingPage />,
    },
  ],
  {
    // basename: 'KRNL-New-Website'
  }
);

const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);
root.render(
  <React.StrictMode>
    <RouterProvider router={router} />
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals(console.log);
